.preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #537db4;
    color: white;
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
}
@-webkit-keyframes pulse {
    0% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
    50% {
        background-color: #537db4;
        color: white;
    }
    100% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
}
@keyframes pulse {
    0% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
    50% {
        background-color: #537db4;
        color: white;
    }
    100% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
}

#search {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
}

#search input[type="search"] {
    position: absolute;
    top: 50%;
    width: 100%;
    color: rgb(255, 255, 255);
    background: rgba(0, 0, 0, 0);
    font-size: 100px;
    font-weight: 300;
    text-align: center;
    border: 0;
    margin: -100px auto 0;
    padding-left: 30px;
    padding-right: 30px;
    outline: none;
}

#search .btn-primary {
    position: absolute;
    width: 30%;
    top: 50%;
    left: 37%;
    margin-top: 61px;
    margin-left: -45px;
}

#search .btn-secondary {
    position: absolute;
    top: 50%;
    left: 65%;
    margin-top: 61px;

}

@media all and (max-width: 980px) {
    #search input[type="search"] {
        font-size: 75px;

    }
}

@media all and (max-width: 720px) {
    #search input[type="search"] {
        font-size: 35px;

    }
}




