/* Define Styles */

.current-page {
  font-size: 1.5rem;
  vertical-align: middle;
}

.country-card-container {
  height: 70px;
  /*cursor: pointer;*/
  position: relative;
  overflow: hidden;
}

.country-name {
  font-size: 1.9rem;
}

.country-region {
  font-size: 1.1rem;
}

.current-page,
.country-name,
.country-region {
  line-height: 1;
}


/* Override some Bootstrap pagination styles */

ul.pagination {
  margin-top: 0;
  margin-bottom: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);

}


.App {
  font-family: sans-serif;
}

li {
    list-style: none;
}

.tree-viewer-container {
    display: flex;
}

.sidebar{
    width: 35%;
    overflow:auto;
    height:90vh;
}

.sidebar ul {
    min-height: calc(100% - 34px);
    padding-left: 20px;
}

.content-viewer-container{
    width: 70%;
    overflow:auto;
    height:90vh;
}

.content-viewer{
    min-height: 90%;
}

.readme-viewer{
   padding: 0 50px;
}

pre {
    white-space: pre-wrap;
    word-wrap: break-word;
    text-align: justify;
    /*padding-left: 30px;*/
    padding: 0;
    margin: 0;
    height: 90vh
}

.hljs {
    height: 100vh;
    padding: 30px 30px 0 30px;
}

.form-container {
    width: 40%;
    margin: auto;
}

.error {
    color: tomato;
}

.success {
    color: blue;
}

@media only screen and (max-width: 640px) {
    .tree-viewer-container {
        display: unset;
    }

    .sidebar{
        width: 100%;
        overflow:auto;
        height:40vh;
    }

    .content-viewer-container{
        width: 100%;

    }
}
